@import "./src/common/scss/variables/variables.scss";

.container {
  font-family: $fontFamily;
  width: 413px;
  height: 100%;
  margin: 50px auto 0;
  padding: 35px;

  background: #ffffff;
  border-radius: 2px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);

  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: flex-start;
}